<template>
     <div class="public_content">
        <div class="public_title"><span>团队成员-添加经纪人</span></div>

        <div class="public_tip orange"><i class="iconfont">&#xe66d;</i><span>提示：经纪人必须注册本站帐号，且未加入任何门店</span></div>


        <el-form ref="ruleform" :model="form" label-width="120px" :rules="rules">
            <el-form-item label="会员登录名：" prop="phone">
                <el-input v-model.number.trim="form.phone" style="width: 140px;"></el-input>
                <el-button type="primary" round size="small" class="getcode" @click="sendMessage" :disabled="disabled">{{ text }}</el-button>
                <span class="public_littleTip"><i class="iconfont">&#xe670;</i>注册的手机号码</span>
            </el-form-item>
            <el-form-item label="手机验证码：" prop="code">
                <el-input v-model="form.code" style="width: 140px;" @blur="checkPhone"></el-input>
                <!-- <div v-show="totalControl">
                    <span class="code_success" v-show="pass"><i class="iconfont">&#xe672;</i>验证通过</span>
                    <span class="code_error" v-show="!pass"><i class="iconfont">&#xe66e;</i>验证失败</span>
                </div> -->

            </el-form-item>
            <el-button type="primary" round @click="onSubmit" class="public_submit">确认添加</el-button> 
        </el-form>
     </div> 
</template>

<script>
import {addCompantMember} from '@/api/shop'
import mixin from "../../mixin/index";
export default {
  mixins: [mixin],
  data() {
    var checkphone = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("手机号不能为空"));
      } else if (!/^1[0-9]{10}$/.test(this.form.phone)) {
        callback(new Error("手机号格式不正确"));
      } else {
        callback();
      }
    };
    var checkCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("验证码不能为空"));
      } else {
        callback();
      }
    };
    return { 
      pass: false,
      totalControl: false,
      form: {
        code: "",
        phone: ""
      },
      rules: {
        phone: [{ validator: checkphone, trigger: "blur" }],
        // code: [{ required: true, message: "验证码不能为空", trigger: "blur" }]
        code: [{ validator: checkCode, trigger: "blur" }]
      }
    };
  }, 
  methods: { 
    checkPhone() {
      // /* 验证验证码 */
      // this.$refs.ruleform.validate(valid => {
      //   if (valid) {
      //     this.totalControl = true;

          
      //     this.pass = true;
      //   }
      // });
    },
    onSubmit() {
      this.$refs.ruleform.validate(valid => {
        if (valid) {
          console.log(this.form)
          addCompantMember(this.form).then(res => {
            if (res.data.code == 1) {
              this.$message.success(res.data.msg)
              this.$router.go(-1)
            }
          })
        } else {
          return false;
        }
      });
    }
  }
};
</script>

<style lang='scss' scoped>
.public_tip {
  margin: 30px 0;
}
.getcode {
  margin-left: 20px;
}
.code_success {
  margin-left: 20px;
  color: #304156;
  i {
    color: #34bfa3;
    margin-right: 6px;
  }
}
.code_error {
  margin-left: 20px;
  color: #f44864;
  i {
    color: #f44864;
    margin-right: 6px;
  }
}
</style>



